.modal {
    justify-content: center;
    display: flex !important;
    position: fixed;
    background-color: rgba(31, 26, 26, 0.795);
    align-items: center;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1000;

}
.modal .modal-video {
    width: 50%;
    height:auto;
    border:none;
    background-color: white;
    border-radius: 5px;
}
.modal .modal-video .video{
    padding: 40px 40px;
}
.modal  span {
    font-size: 30px;
    color: rgba(0, 0, 0, 0.521);
    cursor: pointer;
}
.modal  span:hover {
    
    color: rgba(0, 0, 0, 0.904);
}
.modal .modal-services {
    width: 50%;
    height: 470px;
    border:none;
    overflow-y: scroll;
    background-color: white;
    border-radius: 5px;
}
.modal .modal-services .content {
    padding: 50px 40px;
}
.modal .modal-services .content h3{
    color: blue;
    margin-bottom: 20px;

}
.modal .modal-popup{
    width: auto;
    height: 450px;
    border:none;
    background-color: white;
    border-radius: 5px;

}
.modal .modal-popup .popup {
    padding: 20px;
 
   
}
.modal-popup .popup button {
    background-color: var(--main-color);
    color: white;
    font-size: 14px;
    text-align: left !important;
}
.modal-popup .popup button:focus{
    background-color: var(--secondary-color) ;
    border-color: var(--secondary-color);
    color: var(--main-color);
}